/*
* For styles only loaded in the editor
* These styles are actually inlined using webpack style-loader.  They are not loaded using WordPress enqueuing.
*/
.wp-block-borncreative-header-block .media:hover,
.wp-block-borncreative-header-block .media:focus {
  cursor: pointer; }
  .wp-block-borncreative-header-block .media:hover:after,
  .wp-block-borncreative-header-block .media:focus:after {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: 4px solid lightgreen;
    pointer-events: none; }

.wp-block-borncreative-header-block .media {
  position: relative; }
  .wp-block-borncreative-header-block .media button {
    border: none;
    padding: 0;
    display: block; }
